"use client";
import { useState, useEffect } from "react";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { Send } from "lucide-react";
import axios from "axios";
import Cookies from "js-cookie"; // Import for handling cookies

export default function ChatBot() {
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState("");

  const sendMessage = async () => {
    if (!input.trim()) return;

    const timestamp = new Date().toISOString();
    const newMessage = {
      text: input,
      sender: "user", // Explicitly setting sender type
      timestamp,
    };

    setMessages([...messages, newMessage]); // Update UI instantly
    setInput("");

    try {
      const response = await axios.post(
        "/save_chat",
        { messages: [newMessage] }, // No userId sent, backend will extract it from cookies
        {
          headers: {
            "Content-Type": "application/json",
          },
          withCredentials: true, // Ensure cookies are sent with the request
        }
      );

      const data = await response.data;
      setMessages([...messages, newMessage, { sender: "ai", text: data.reply, timestamp: new Date().toISOString() }]);
    } catch (error) {
      console.error("Error sending message:", error);
    }
  };

  return (
    <div className="flex flex-col h-screen mx-auto p-4 bg-gray-900 text-white rounded-lg shadow-lg border border-gray-700 custom-scrollbar">
      <h1 className="text-2xl font-semibold text-center mb-4 text-gray-200">
        AI-Powered Health Assistant
      </h1>
      <div className="flex-1 overflow-y-auto space-y-3 p-2 bg-gray-800 rounded-lg shadow-inner custom-scrollbar">
        {messages.map((msg, idx) => (
          <Card
            key={idx}
            className={`rounded-2xl p-3 shadow-md text-sm max-w-xs ${
              msg.sender === "user"
                ? "bg-gray-500 text-white"
                : "bg-gray-700 text-gray-300"
            }`}
          >
            <CardContent>
              <p className="text-xs text-gray-400">
                {new Date(msg.timestamp).toLocaleTimeString()}
              </p>
              <p>{msg.text}</p>
            </CardContent>
          </Card>
        ))}
      </div>
      <div className="flex gap-3 mt-4 bg-gray-800 p-3 rounded-lg shadow-md">
        <Input
          value={input}
          onChange={(e) => setInput(e.target.value)}
          placeholder="Type a message..."
          className="flex-1 bg-gray-700 text-white border-none focus:ring-2 focus:ring-blue-400 rounded-lg p-2"
        />
        <Button
          onClick={sendMessage}
          className="p-3 bg-blue-500 hover:bg-blue-600 text-white rounded-lg shadow-md"
        >
          <Send size={20} />
        </Button>
      </div>
    </div>
  );
}
